'use client'

import { createClientComponentClient } from '@/lib/supabase'
import { useRouter } from 'next/navigation'
import { useState } from 'react'

export default function LoginPage() {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const router = useRouter()
  const supabase = createClientComponentClient()

  const handleSignIn = async () => {
    const { error } = await supabase.auth.signInWithPassword({
      email,
      password,
    })

    if (error) {
      console.error('Error signing in:', error.message)
      alert('Error signing in: ' + error.message)
    } else {
      router.push('/')
      router.refresh()
    }
  }

  const handleSignUp = async () => {
    const { error } = await supabase.auth.signUp({
      email,
      password,
      options: {
        emailRedirectTo: `${location.origin}/auth/callback`,
      },
    })

    if (error) {
      console.error('Error signing up:', error.message)
      alert('Error signing up: ' + error.message)
    } else {
      alert('Check your email for a confirmation link!')
    }
  }

  return (
    <div className="flex flex-col items-center justify-center min-h-screen py-2">
      <h1 className="text-2xl font-bold mb-4">Login</h1>
      <input
        type="email"
        name="email"
        placeholder="Email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        className="p-2 border border-gray-300 rounded mb-2 w-80"
      />
      <input
        type="password"
        name="password"
        placeholder="Password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        className="p-2 border border-gray-300 rounded mb-4 w-80"
      />
      <button onClick={handleSignIn} className="bg-blue-500 text-white p-2 rounded w-80 mb-2">
        Sign In
      </button>
      <button onClick={handleSignUp} className="bg-green-500 text-white p-2 rounded w-80">
        Sign Up
      </button>
    </div>
  )
}
